import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import { roomsFetchRequest, roomAddRequest, roomDelRequest } from "../actions";
import { Loader } from "../../../Common/Loader";
import { Table, Button, Row, Col, Modal, Input } from "antd";
import style from "./index.module.scss";

export const Rooms = props => {
  const [showModal, setShowModal] = useState(false);
  const [title, setTitle] = useState();

  useEffect(() => {
    props.roomsFetchRequest();
    // eslint-disable-next-line
  }, []);

  const handleAdd = () => {
    setShowModal(true);
  };

  const handleOk = e => {
    props.roomAddRequest(title);
    setTitle(null);
    setShowModal(false);
  };

  const handleCancel = e => {
    setTitle(null);
    setShowModal(false);
  };

  const handleTitle = e => {
    setTitle(e.currentTarget.value);
  }

  const handleCreateClick = (id, title) => {
    window.open(
      'https://localhost:9001?rid=' + id + "&open=true&username=" + props.user.name + "&title=" + title,
      '_blank'
    );
  }

  const handleInviteClick = (id) => {
    window.open(
      'https://localhost:9001?rid=' + id + "&open=false&username=" + props.user.name,
      '_blank'
    );
  }

  const roomDel = (id) => {
    props.roomDelRequest(id);
  }

  const columns = [
    {
      title: "ID",
      dataIndex: "_id",
      key: "id"
    },
    {
      title: "Title",
      dataIndex: "title",
      key: "title"
    },
    {
      title: "State",
      dataIndex: "state",
      key: "state"
    },
    {
      title: "Create",
      key: "create",
      render: (rowData) => (
        (props.user.role === 1) ? (
          <Button type="primary" onClick={() => handleCreateClick(rowData._id, rowData.title)}>Create</Button>
        ) : (
          <Button type="primary" disabled={props.user.id !== rowData.creator} onClick={() => handleCreateClick(rowData._id)}>Create</Button>
        )
      )
    },
    {
      title: "Invite",
      key: "invite",
      render: (rowData) => (
        <Button type="primary" onClick={() => handleInviteClick(rowData._id)}>Invite</Button>
      )
    },
    {
      title: "Delete",
      key: "delete",
      render: (rowData) => (
        (props.user.role === 1) ? (
          < Button type="primary" onClick={() => roomDel(rowData._id)}> Delete</Button>
        ) : (
          < Button type="primary" disabled={props.user.id !== rowData.creator} onClick={() => roomDel(rowData._id)}> Delete</Button >
        )
      )
    }
  ];

  return (
    <div>
      <div>
        <Row type="flex" justify="space-between">
          <Col span={4}><h1>Rooms</h1></Col>
          <Col span={4}></Col>
          <Col span={4}></Col>
          <Col span={4}>
            <Button onClick={handleAdd} type="primary">
              Open New Room
            </Button>
          </Col>
        </Row>
      </div>
      <div className={style.roomsList}>
        <Loader isLoading={props.isLoading}>
          <Table columns={columns} dataSource={props.rooms} rowKey="_id" />
        </Loader>
      </div>
      <Modal
        title="New Room"
        visible={showModal}
        onOk={handleOk}
        onCancel={handleCancel}
      >
        <p>Please input the title</p>
        <Input value={title} onChange={handleTitle} />
      </Modal>
    </div>
  );
};

Rooms.propTypes = {
  isLoading: PropTypes.bool.isRequired,
  rooms: PropTypes.array,
  roomsFetchRequest: PropTypes.func.isRequired
};

const mapStateToProps = state => ({
  isLoading: state.cabinet.rooms.isLoading,
  rooms: state.cabinet.rooms.data,
  user: state.auth.signin.user
});

export const HomeContainer = withRouter(
  connect(
    mapStateToProps,
    { roomsFetchRequest, roomAddRequest, roomDelRequest }
  )(Rooms)
);
